










import { Component, Emit, Vue } from 'vue-property-decorator';
import Progress from '../components/Progress.vue';

@Component({
  components: {
    Progress,
  },
})
export default class Start extends Vue {
  public toFinder(): void {
    this.$router.push({
      path: '/finder',
    });
  }

  @Emit('updateStatus')
  updateStatus(): [] {
    return [];
  }

  mounted(){
    this.updateStatus();
  }

  public toLogin(): void {
    this.$router.push({
      path: '/login',
    });
  }
}
